.menu {
  margin: 0;
  background: linear-gradient(
    to top,
    rgb(17, 62, 26),
    rgba(189, 54, 52, 1) 70%
  );
  width: 100%;
  height: 200vh;
  position: relative;
  background-size: cover;
}

.more {
  background-color: rgb(17, 62, 26);
  width: 200px;
  height: 4vw;
  border: none;
  border-radius: 20px;
  margin-right: 4%;
  margin-top: 3%;
  cursor: pointer;
  color: rgba(190, 211, 219, 1);
  font-family: Mountains of Christmas;
  font-size: 40px;
  font-weight: 600;
  box-shadow: 5px 5px 50px rgb(96, 13, 13);
  margin-left: 5%;
}

.more:hover {
  transform: scale(1.2, 1.2);
  transition: 0.4s;
}

.vert {
  border-radius: 20px;
  height: 700px;
  box-shadow: 5px 5px 50px rgb(96, 13, 13);
}

.hor {
  border-radius: 20px;
  height: 500px;
  box-shadow: 5px 5px 50px rgb(96, 13, 13);
}

.card {
  margin-top: 10%;
  margin-left: 30px;
  margin-right: 30px;
}

.card:first-child {
  margin-left: 10%;
}

.card:last-child {
  margin-right: 10%;
}

.card:hover {
  transform: scale(1.2, 1.2);
  transition: 0.4s;
}

.menu nav {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.lights {
  height: 300px;
  width: 400px;
  margin-bottom: -16%;
  transform: translateY(-30%) translateX(150%);
}

.colLights {
  margin-bottom: -7%;
  margin-top: 3%;
}
