.menu {
  background-color: rgba(189, 54, 52, 1);
  width: 100%;
  height: 90vh;
  position: relative;
  background-size: cover;
  margin-top: -2%;
  padding: 0;
}

.menu2 {
  height: 200vh;
  background: linear-gradient(
    to bottom,
    rgba(190, 211, 219, 1) 10%,
    rgba(189, 54, 52, 1)
  );
}

.kickOff {
  font-family: Mountains of Christmas;
  text-align: center;
  padding-top: 0%;
  font-size: 40px;
}

.snowman {
  height: 300px;
  translate: -30%;
}

.santaSleigh {
  transform: translateY(-25%);
  margin-bottom: -30%;
}

.santa {
  height: 200px;
  transform: translateY(0);
  transform: translateX(30%);
}

img.snow {
  width: 100%;
  height: auto;
  padding-bottom: 0;
  bottom: 0;
  margin-bottom: 0;
  transform: translateY(-60%);
}

.vert {
  border-radius: 20px;
  height: 700px;
  box-shadow: 5px 5px 50px rgb(96, 13, 13);
}

.hor {
  border-radius: 20px;
  height: 500px;
  box-shadow: 5px 5px 50px rgb(96, 13, 13);
}

.card {
  margin-top: 10%;
  margin-left: 30px;
  margin-right: 30px;
}

.card:first-child {
  margin-left: 10%;
}

.card:last-child {
  margin-right: 10%;
}

.card:hover {
  transform: scale(1.2, 1.2);
  transition: 0.4s;
}

.menu2 nav {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.lights {
  height: 300px;
  margin-bottom: -10%;
  transform: translateX(5%) rotate(10deg);
}

.lights2 {
  height: 200px;
  margin-bottom: -10%;
  transform: translateX(-25%) rotate(-20deg);
}

.more {
  background-color: rgb(17, 62, 26);
  width: 200px;
  height: 4vw;
  border: none;
  border-radius: 20px;
  margin-right: 4%;
  margin-top: 3%;
  cursor: pointer;
  color: rgba(190, 211, 219, 1);
  font-family: Mountains of Christmas;
  font-size: 40px;
  font-weight: 600;
  box-shadow: 5px 5px 50px rgb(96, 13, 13);
  margin-left: 45%;
}

.more:hover {
  transform: scale(1.2, 1.2);
  transition: 0.4s;
}
